nav {
    width: 100vw;
    height: 5rem;
    display: grid;
    place-items: center;
    background: #058383;
    position: grid;
    top: 0;
    left: 0;
    z-index: 9;
    box-shadow: 0 1rem 1.5rem hsla(var(--primary-hue), 68%, 42%, 20%);
    transition: var(--transition);
}

.nav_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav_logo {
    width: 2.5rem;
    border-radius: 50%;
    overflow: hidden;
}

.nav_menu {
    display: flex;
    gap: 3.5rem;
}

#theme_icon {
    background: transparent;
    color: white;
    font-size: 2rem;
    cursor: pointer;
}

.nav2 {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.5rem 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 10rem;
    backdrop-filter: blur(3px);
    top: 85%;
}

.nav2 a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1rem;
}

.nav2 a:hover {
    background: white;
    color: #058383;
}

.nav2 a.active {
    background: var(--color-light);
    color: #058383;
}


/* CUSTOMISION */

.customize-theme {
    background: rgba(255, 255, 255, 0.5);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    text-align: center;
    display: none;
    place-items: center;
    font-size: 0.9rem;
    z-index: 100;
}

.customize-theme .card {
    background: var(--color-light);
    padding: 3rem;
    border-radius: 2rem;
    width: 50%;
}


/* CUSTOMISION COLOR */

.customize-theme .color {
    margin-top: 2rem;
}

.customize-theme .choose-color {
    background: var(--color-white);
    padding: 0.5rem;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.customize-theme .choose-color div {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
}

.customize-theme .choose-color div:nth-child(1) {
    background: hsl(52, 75%, 60%);
}

.customize-theme .choose-color div:nth-child(2) {
    background: hsl(602, 75%, 60%);
}

.customize-theme .choose-color div:nth-child(3) {
    background: hsl(352, 75%, 60%);
}

.customize-theme .choose-color div:nth-child(4) {
    background: hsl(152, 75%, 60%);
}

.customize-theme .choose-color div:nth-child(5) {
    background: hsl(202, 75%, 60%);
}

.customize-theme .choose-color div.active {
    border: 5px solid white;
}


/* CUSTOMISION BACKGROUND */

.customize-theme .background {
    margin-top: 2rem;
}

.customize-theme .choose-bg {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.5rem;
}

.customize-theme .choose-bg>div {
    padding: 1rem;
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 0.4rem;
    cursor: pointer;
}

.customize-theme .choose-bg>div.active {
    border: 2px solid #058383;
}

.customize-theme .choose-bg .bg-1 {
    background: white;
    color: black;
}

.customize-theme .choose-bg .bg-2 {
    background: rgb(30, 44, 56);
    color: white;
}

.customize-theme .choose-bg .bg-3 {
    background: hsl(197, 16%, 18%);
    color: white;
}

.customize-theme h6 {
    color: white;
}

.customize-theme .choose-bg>div div {
    width: 2rem;
    height: 2rem;
    border: 2px solid #058383;
    border-radius: 50%;
    margin-right: 1rem;
}


/* MEDIA QUERIES  TABLETS AND PHONES */

@media screen and (max-width:1024px) {
    .nav_menu {
        display: none;
    }
    .nav2 {
        position: fixed;
        display: flex;
    }
}


