.about_container {
    display: grid;
    grid-template-columns: 35% 53%;
    gap: 12%;
}


/* .about_right h2 {
    margin-top: -5rem;
    margin-left: -33px;
} */

.about_portrait {
    height: fit-content;
    max-height: 40rem;
    border: 1rem solid var(--color-white);
    transition: var(--transition);
    margin-top: 5rem;
    width: 23rem;
}

.about_portrait:hover {
    border-width: 0;
}

.about_cards {
    margin: 3rem 0 1.5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

#about h2 {
    margin-top: -5rem;
}

.card.about_card {
    text-align: center;
    width: 8rem;
    height: 8rem;
}

.about_card-icon {
    font-size: 1.3rem;
    color: var(--color-black);
    border-radius: var(--border-radius-3);
    width: fit-content;
    margin-inline: auto;
    padding: 0.1rem;
    display: flex;
    align-items: center;
    transition: var(--transition);
}

.card.about_card:hover .about_card-icon {
    background: #058383;
    color: var(--color-white);
}

.card.about_card h5 {
    margin: 0.1rem 0;
}

.about_right p {
    margin-bottom: 0.8rem;
}

.about_right .btn {
    margin-top: 2.5rem;
}


/* MEDIA QUERIS (TABLETS) */

@media screen and (max-width: 1024px) {
    .about_left {
        display: none;
    }
    .about_container {
        grid-template-columns: 1fr;
    }
    /* .about_right h2 {
        margin-left: 240px;
    } */
    .about_cards {
        margin-left: 40px;
    }
}


/* MEDIA QUERIES (PHONES) */

@media screen and (max-width: 600px) {
    .about_cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        margin-left: 10px;
    }
    .about_right .btn {
        margin-top: 2rem;
    }
    /* .about_right h2 {
        margin-left: 80px;
    } */
}