.services_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem 4rem;
}

#services h2 {
    margin-top: -5rem;
}

.card.service {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
}

.service_icon {
    /* background: #058383;
    padding: 0.4rem;
    border-radius: var(--border-radius-3);
    font-size: 1rem;
    color: var(--color-white);
    transition: var(--transition); */
    width: 5rem;
    font-size: 1rem;
    aspect-ratio: 1/1;
    background: #058383;
    color: var(--color-white);
    display: grid;
    place-items: center;
    border-radius: var(--border-radius-3);
}

.service:hover .service_icon {
    background: var(--color-light);
    color: #058383;
}

.service h4 {
    margin-bottom: 0.3rem;
}


/* MEDIA QUERIES (TABLETS) */

@media screen and (max-width:1024px) {
    .services_container {
        gap: 1.5rem;
    }
}


/* MEDIA QUERIES (PHONES) */

@media screen and (max-width:600px) {
    .services_container {
        grid-template-columns: 1fr;
        gap: 1.2rem;
    }
    .card.service {
        gap: 1.2rem;
    }
}