.permalinks a {
    color: var(--color-light);
}

footer {
    background: #058383;
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 0rem;
}

.footer_logo {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto;
    margin: 0 auto 3rem;
}

.footer_socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_socials a {
    /* background: var(--color-light);
     width: 1.6rem;
     border-radius: 0.3rem;
     color: #058383;
     place-items: center; */
    width: 1.6rem;
    aspect-ratio: 1/1;
    background: var(--color-light);
    color: var(--color-black);
    display: grid;
    place-items: center;
    border-radius: var(--border-radius-3);
}

.footer_socials a:hover {
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-white);
}

.footer_copyrght {
    margin-bottom: 4rem;
    color: var(--color-white);
}


