.portofolio_cta {
    display: flex;
    align-items: center;
    gap: 1.2rem;
    margin-top: 20px;
    font-size: 10px;
    font-weight: 600;
    padding: 3px 3px;
}

#portofolio h2 {
    margin-top: -5rem;
}

.portofolio_container {
    display: grid;
    /* grid-template-columns: repeat(3, 1fr); */
    grid-template-columns: 31% 31% 31%;
    gap: 2.5rem;
}

.portofolio_item {
    background: var(--color-white);
    padding: 1rem;
    border-radius: 1rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portofolio_item:hover {
    border-color: #058383;
    background: transparent;
}

.portofolio_item-image {
    border-radius: 1rem;
    overflow: hidden;
}

.portofolio_item h5 {
    margin-top: 10px
}

.btnlight:hover {
    border-color: white
}


/* HIDE PORTOFOLIO ON TABLETS AND PHONES */

@media screen and (max-width: 1024px) {
    .portofolio_container {
        grid-template-columns: 1fr 1fr;
        gap: 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .portofolio_container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}