.container.testimonials_container {
    width: 40%;
    padding-bottom: 4rem;
}

#testimonials h2 {
    margin-top: -5rem;
}

.client_avatar {
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid #058383;
}

.testimonial {
    background: var(--color-light);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client_riview {
    /* font-weight: 300; */
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
    background: #058383;
}


/* MEDIA QUERIEN TABLETS */

@media screen and (max-width: 1024px) {
    .container.testimonials_container {
        /* width: var(--container-width-sm); */
        /* grid-template-columns: 1fr 1fr; */
        width: 30rem;
    }
}


/* MEDIA QUERIES PHONES */

@media screen and (max-width: 600px) {
    .container.testimonials_container {
        /* width: var(--container-width-sm); */
        /* grid-template-columns: 1fr; */
        width: 20rem;
    }
}